import React from 'react';
import _ from 'lodash';
import Head from 'next/head';
import RadioButtonGroup from '../components/RadioButtonGroup';
import data from '../db/albion';

class Home extends React.Component {
  state = { Categories: {}, Category: '', SubCategories: {}, SubCategory: '' };

  componentDidMount() {
    this.setState({ Categories: data });
  }

  handleCategoryChange = event => {
    console.log(event.target);
    const { Categories } = this.state;
    const { name, value } = event.target;
    this.setState({ SubCategories: Categories[value], [name]: value });
  };

  render() {
    return (
      <div>
        <Head>
          <title>Albion Tools</title>
          <meta name="viewport" content="initial-scale=1.0, width=device-width" />
        </Head>

        <RadioButtonGroup
          data={data}
          handleCategoryChange={this.handleCategoryChange}
          value={this.state.Category}
          name="Category"
        />
        {this.state.Category && (
          <RadioButtonGroup data={this.state.SubCategories} handleCategoryChange={this.handleCategoryChange} />
        )}
        {/* NOTE: ([name]) is array destruturing, it takes the first element of the
        arrays and names it so we can properly identify it for filtering, remember 
        the name is arbitrary */}
      </div>
    );
  }
}

export default Home;
